.set-item{
    height: 100%;
    width: 100%;
    background: #333333;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 25px;
    color: #ffffff;
    font-size: 1.5rem;
    transition-duration: 0.4s;
    position: relative;
    transition-property: background, box-shadow ;
    transition-duration: 0.4s, 1s;

}

.set-item:hover{
    background: rgb(255, 230, 0);
    box-shadow: 0 0 10px rgb(255, 230, 0);
}

.set-item:hover *{
    color: black;
}

.type-id{
    position: absolute;
    right: 10px;
    bottom: 10px;
    font-size: 0.8rem;
}